/* MAIN LAYOUT */
.app-container {
  position: relative;
  min-height: 100vh;
  background: var(--smoky-black);
  padding: 15px;
}

.content-wrapper {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 25px;
  margin-bottom: 75px;
  position: relative;
}

.main-content-container {
  position: relative;
  background: transparent;
}

/* Ensure parent containers don't clip the blur effect */
.app-container, 
.content-wrapper,
.main-content-container {
  overflow: visible !important;
}

/* Force hardware acceleration for better blur performance */
.navbar-glass {
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.page-content {
  flex: 1;
  animation: fadeIn 0.3s ease;
}

article {
  background: transparent;
  border: none;
  box-shadow: none !important; /* Force remove shadow */
  padding: 0;
}

/* RESPONSIVE ADJUSTMENTS */
@media (min-width: 1250px) {
  .app-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px 25px;
  }
  
  .content-wrapper {
    margin-bottom: 0;
    grid-template-columns: 350px 1fr;
  }
}

@media (max-width: 1024px) {
  .content-wrapper {
    grid-template-columns: 1fr;
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}